  background-size: contain;
}
.header .right {
  color: #ff2a00;
}
.ptb20 {
  padding: 0.2rem 0;
}
.c_orange {
  color: #ff2a00;
}
.mt42 {
  margin-top: 0.42rem;
}
.mt80 {
  margin-top: 0.8rem;
}
.w-btn {
  color: #ffffff;
  font-size: 0.32rem;
  height: 0.98rem;
  line-height: 0.98rem;
  text-align: center;
  border-radius: 0.98rem;
  width: 100%;
  -webkit-box-shadow: rgba(0, 0, 0, 0.3) 0 0 0.1rem;
          box-shadow: rgba(0, 0, 0, 0.3) 0 0 0.1rem;
  background: #ff2a00;
}
.s-btn {
  color: #ffffff;
  font-size: 0.28rem;
  height: 0.68rem;
  line-height: 0.68rem;
  text-align: center;
  border-radius: 0.68rem;
  width: 1.8rem;
  background: #ff2a00;
}
.wi-btn {
  font-size: 0.32rem;
  height: 0.98rem;
  line-height: 0.98rem;
  text-align: center;
  width: 100%;
  color: #ff2a00;
}
.login {
  color: #333333;
  padding: 0 0.3rem;
  line-height: 0.6rem;
}
.login .bold_tit {
  font-size: 0.54rem;
  padding: 0.4rem 0 0.2rem 0;
}
.login .wel_tit {
  font-size: 0.32rem;
}
.login p {
  position: relative;
}
.login .ipt {
  width: 100%;
  padding: 0.2rem 0;
  margin-top: 0.3rem;
  font-size: 0.32rem;
  border-bottom: 1px solid #f5f5f5;
}
.login .close {
  position: absolute;
  top: 0.5rem;
  right: 0;
  width: 0.32rem;
  height: 0.32rem;
  display: block;
  background: url(data:image/png;base64,) no-repeat 50% 50%;
  background-size: contain;
}
.login .s-btn {
  position: absolute;
  right: -2rem;
  bottom: 0;
}
.login .ipt-code {
  width: 70%;
}
.login .remember {
  color: #999999;
}
.login .forgetpwd {
  float: right;
  color: #ff2a00;
}
